* {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}

body {
    font-family: Roboto;
}

.header,
.section,
.main {
    width: 85vw;
    margin: 0 auto;
}

.header {
    margin-top: 15px;
    margin-bottom: 15px;
}

h2 {
    font-size: 22px;
}

input {
    height: 50px;
    border-radius: 2px;
    border: solid 1px #e9e9e9;
    font-size: 16px;
    width: 150px;
    text-indent: 20px;

    &[type='date'] {
        height: 48px;
        text-indent: 5px;
    }
}

.form {
    margin-top: 50px;
    margin-bottom: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
    align-content: center;
    font-size: 16px;
    position: relative;

    &_data::before {
        content: 'Выберите период';
        position: absolute;
        top: -20px;
    }

    &_type {
        position: relative;

        input {
            width: 100%;
        }

        &::before {
            content: 'Выберите тип';
            position: absolute;
            top: -20px;
        }
    }

    &_btn {
        justify-self: end;
        width: 90%;
        display: flex;
        justify-content: space-between;
    }
}

.btn {
    width: 140px;
    height: 50px;
    border-radius: 4px;
    background-color: #fff;
    border: solid 1px #87a2e4;
    transition: 0.2s;
    font-size: 13px;
    color: #87a2e4;
    text-transform: uppercase;
}

.btn:hover {
    background-color: #87a2e4;
    color: #fff;
}

.section {
    margin-top: 5px;
    margin-bottom: 5px;
}

.container {
    width: 100%;
    display: grid;
    grid-template-columns: 0.5fr 1.5fr 0.5fr;
    justify-items: center;
    border-bottom: 1px solid #e9e9e9;
    padding-top: 50px;
    padding-bottom: 50px;
    background-color: #fff;
    transition: background-color .2s;

    &:first-child {
        border-top: 1px solid #e9e9e9;
    }
}

.type {
    color: #d51d4c;
    font-size: 16px;
    text-transform: uppercase;
    width: 30px;
    height: 30px;
    align-self: flex-start;
}

.info {
    width: 470px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.date {
    display: flex;
    font-size: 13px;
    font-weight: lighter;
    color: rgba(63, 63, 63, 0.6);
    width: 100%;

    &_load {
        min-width: 40%;
    }
}

.date_load::before {
    content: 'Дата загрузки: ';
}

.date_container::before {
    content: 'Хранить до: ';
}

.file_name {
    font-size: 18px;
    margin-top: 10px;
    margin-bottom: 20px;
    color: #3f3f3f;
}

.sizefile {
    text-transform: uppercase;
    font-size: 13px;
    color: #87a2e4;
}

.user_name {
    color: rgba(63, 63, 63, 0.6);
    font-size: 13px;
}

.load_del {
    place-self: center;
    width: 60%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.load, .delete {
    width: 30px;
    height: 30px;
    background-color: #fff;
    transition: 0.2s;
    &:hover{
        transform: translateY(-2px);
    }
}


@media only screen and (max-width: 320px) {

    .header,
    .section,
    .main {
        width: 90vw;
        margin: 0 auto;
    }

    .form {
        width: 100%;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-row-gap: 30px;

        &_data {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &_type::before {}

        input {
            width: 100%;
        }

        &_btn {
            display: flex;
            justify-content: space-between;
            width: 100%;
        }

        .btn {
            width: 138px;
        }
    }

    .container {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 0.5fr 1.5fr;
        grid-template-areas: 'type btn'
            'main main';
    }

    .type {
        grid-area: type;
    }

    .load_del {
        grid-area: btn;
    }

    .info {
        grid-area: main;
        width: 90%;
    }

    .date {
        flex-direction: column;
    }
}